.ButterflyChart {
    display: grid;
    grid-template-columns: 1fr 2px 1fr; 
    grid-template-rows: auto 1fr;
    justify-items: center;
    align-items: center;
    padding: 20px; 
    padding-top: 0;
    width: 800px; 
    font-family: 'Athiti', sans-serif;
}

.Title {
    grid-column: 1 / span 3;
    grid-row: 1;
}

.ButterflyChart-left {
    grid-column: 1;
    grid-row: 2; 
    width: 100%; 
}

.ButterflyChart-divider {
    grid-column: 2; 
    grid-row: 2; 
    background-color: darkslategray; 
    height: 100%; 
    width: 100%; 
}

.ButterflyChart-right {
    grid-row: 2; 
    grid-column: 3;
    width: 100%; 
}

@media screen and (max-width: 850px) { 
    .ButterflyChart {
        width: 700px;
    }
}

@media screen and (max-width: 750px) { 
    .ButterflyChart {
        width: 600px;
    }
}

@media screen and (max-width: 650px) { 
    .ButterflyChart {
        width: 500px;
    }
}

@media screen and (max-width: 550px) { 
    .ButterflyChart {
        width: 400px;
    }
}

@media screen and (max-width: 450px) { 
    .ButterflyChart {
        width: 300px;
    }
}

@media screen and (max-width: 350px) { 
    .ButterflyChart {
        width: 200px;
    }
}
.ChartRow {
    display: flex;
    align-items: center;
    margin-bottom: 5px; 
    min-height: 50px;
}
    
.ChartRow:first-child {
    margin-top: 10px;
}

.ChartRow:last-child {
    margin-bottom: 10px;
}

.ChartRow-left {
    display: flex; 
    justify-content: flex-end;
}
  
.u-dottedLine {
    height: 50%; 
    border-top: 1px dotted darkslategray; 
    min-width: 20px;
    flex-grow: 1;
    margin-left: 4px; 
    margin-right: 4px; 
}

.ChartRow-bar {
    display: flex; 
    align-items: center;
    justify-content: center;
    border: .5px solid darkslategray;
    box-sizing: border-box;
    height: 40px;
    padding: 2px;
    color: white;
    -webkit-transition: .2s;
    transition: .2s; 
    text-overflow: clip;
}
  
.LeftBar {
    border-right: 0; 
}
  
.RightBar {
    border-left: 0; 
}
  
.ChartRow-bar:hover {
    box-shadow: 0 3px 10px rgba(0,0,0,.2);
    -webkit-transform: translate3d(0,-2px,0);
            transform: translate3d(0,-2px,0);
    cursor: pointer;
}
  

